  position: relative;
  .affiliate-secondary-banner-content {
    width: 40%;
    text-align: center;
    position: absolute;
    top: 50%;
    margin-left: 60px;
    transform: translate(0%, -50%);
    .title {
      border-bottom: 4px solid var(--v-primary-base);
      margin: 0;
      margin-bottom: 5px;
      font-size: 18px;
    }
  }
}

@media (max-width: 1263px) {
  .affiliate-secondary-banner-container .affiliate-secondary-banner-content .title {
